import Link from "next/link"

import { Button } from "@/components/ui/button"
import { getDistinctLineIds } from "@/features/line-dashboard"

export default async function Page() {
  let lineIds = []

  try {
    lineIds = await getDistinctLineIds()
  } catch (error) {
    console.error("Failed to load line identifiers", error)
    lineIds = []
  }

  const firstLineId = lineIds[0] ?? null

  return (
    <section className="grid gap-4">
      <div className="rounded-xl border bg-card p-6 shadow-sm">
        <h1 className="text-2xl font-semibold tracking-tight">ESOP Dashboard</h1>
        <p className="mt-2 text-sm text-muted-foreground">
          Select a production line from the switcher to explore its live ESOP status dashboard.
        </p>
        {firstLineId ? (
          <div className="mt-4 flex flex-wrap items-center gap-2">
            <Button asChild>
              <Link href={`/${firstLineId}/ESOP_Dashboard/status`}>
                View line {firstLineId}
              </Link>
            </Button>
            {lineIds.length > 1 ? (
              <span className="text-xs text-muted-foreground">
                {lineIds.length - 1} other line{lineIds.length - 1 === 1 ? "" : "s"} available via the switcher.
              </span>
            ) : null}
          </div>
        ) : (
          <p className="mt-4 text-sm text-muted-foreground">
            No production lines are currently configured. Please contact an administrator to connect ESOP data.
          </p>
        )}
      </div>
    </section>
  )
}
